            <h4>Honors & Awards</h4>
            <ul>
              <li v-for="(a, i) in awards" :key="i">{{ a }}</li>
            </ul>
          </div>

          <div class="card reveal">
            <h4>Teaching</h4>
            <ul>
              <li v-for="(t, i) in teaching" :key="i">{{ t }}</li>
            </ul>
          </div>

          <div class="card reveal">
            <h4>Peer Review</h4>
            <ul>
              <li v-for="(r, i) in peerReview" :key="i">{{ r }}</li>
            </ul>
          </div>
        </section>

        <!-- Contact -->
        <section class="card reveal contact-card">
          <h3 class="section-title">Contact</h3>
          <p>Email: <a :href="'mailto:' + contact.email">{{ contact.email }}</a></p>
          <p>Affiliation: Robotics and Soft Technologies Research Center, Azad University of Tabriz</p>

          <div class="contact-actions">
            <a :href="links.github" target="_blank" rel="noopener">GitHub</a>
            <a :href="links.scholar" target="_blank" rel="noopener">Google Scholar</a>
            <a :href="links.linkedin" target="_blank" rel="noopener">LinkedIn</a>
            <a :href="links.orcid" target="_blank" rel="noopener">ORCID</a>
          </div>
        </section>

        <footer class="footer">
