<style scoped>
/* Dark-mode only styles */
:root {
  --bg: #0b1220;
  --card: #071022;
  --muted: #9aa4b2;
  --text: #e6eef6;
  --accent: #06b6d4;
  --glass: rgba(255, 255, 255, 0.03);
  --shadow: 0 6px 20px rgba(2, 6, 23, 0.6);
}

.page-root {
  background: linear-gradient(180deg, var(--bg), var(--bg));
  color: var(--text);
  min-height: 100vh;
  font-family: Inter, ui-sans-serif, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
}

/* Topbar */
.topbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 22px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.03);
  background: transparent;
  position: sticky;
  top: 0;
  z-index: 20;
}
.topbar .brand {
  display: flex;
  gap: 12px;
  align-items: center;
}
.logo {
  width: 64px;
  height: 64px;
  border-radius: 8px;
  object-fit: cover;
  box-shadow: var(--shadow);
  border: 1px solid rgba(255, 255, 255, 0.04);
}
.name {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 700;
}
.tagline {
  margin: 0;
  font-size: 0.9rem;
  color: var(--muted);
}

/* Layout */
.layout {
  display: grid;
  grid-template-columns: 260px 1fr;
  gap: 28px;
  max-width: 1200px;
  margin: 28px auto;
  padding: 0 18px;
}
.sidebar {
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border-radius: 10px;
  padding: 18px;
  box-shadow: var(--shadow);
  height: fit-content;
  transition: transform .25s ease, opacity .25s ease;
}
.sidebar-inner { display: flex; flex-direction: column; gap: 12px; align-items: start; }
.profile { width: 120px; height: 120px; border-radius: 8px; object-fit: cover; }
.sidebar-name { margin: 6px 0 0; font-weight: 700; font-size: 1.05rem; }
.sidebar-sub { margin: 0; color: var(--muted); font-size: 0.92rem; }

/* socials */
.socials { display:flex; flex-direction: column; gap: 8px; margin-top: 8px; width:100%; }
.socials a { color: var(--text); text-decoration: none; font-size: 0.95rem; }
.socials a:hover { color: #D4AF37; text-decoration: underline; }

/* mini stats */
.mini-stats { display:flex; gap: 12px; margin-top: 12px; width:100%; justify-content: space-between; }
.mini-stats div { text-align:center; font-size: 0.85rem; color: var(--muted); }
.mini-stats strong { display:block; font-size:1.05rem; color:var(--text); }

/* mobile toggle */
.mobile-toggle { display:none; margin-top:4px; background:transparent; border:1px solid rgba(255,255,255,0.04); padding:6px 8px; border-radius:6px; cursor:pointer; color:var(--text); }

/* Content */
.content { padding-bottom: 40px; }
.card { background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01)); border-radius: 10px; padding: 18px; margin-bottom: 18px; box-shadow: var(--shadow); transition: transform .28s cubic-bezier(.2,.9,.3,1), opacity .28s; opacity: 0; transform: translateY(12px) scale(.995); }
.card.in-view { opacity: 1; transform: translateY(0) scale(1); }

.section-title { margin: 0 0 8px; font-size:1.05rem; font-weight:700; }
.lead { margin: 8px 0 0; color: var(--muted); line-height:1.6; }
.intro-grid { display:flex; justify-content:space-between; gap: 12px; align-items: center; }
.quick-links { display:flex; gap:8px; flex-wrap:wrap; }
.chip { padding:8px 10px; border-radius: 999px; background: var(--glass); color: var(--text); text-decoration:none; font-size:0.9rem; border:1px solid rgba(255,255,255,0.03); }

/* news */
.news { list-style:none; padding:0; margin:0; display:flex; flex-direction:column; gap:8px; }
.news-item { display:flex; gap:12px; align-items:flex-start; color:var(--muted); }
.news-date { font-weight:700; color:var(--accent); min-width:84px; }

/* tags */
.tags { display:flex; gap:8px; flex-wrap:wrap; }
.tag { padding:8px 10px; border-radius:999px; background:var(--glass); font-size:0.88rem; color:var(--text); }

/* publications */
.publications { list-style: none; padding-left: 0; margin: 0; display:flex; flex-direction:column; gap:12px; }
.publication { display:flex; justify-content:space-between; gap:8px; align-items:flex-start; }
.pub-title { font-weight:600; color:var(--text); }
.pub-authors { color: var(--muted); font-size:0.9rem; }
.pub-links a { margin-left: 10px; color: var(--accent); text-decoration:none; }
.pub-links a:hover { text-decoration: underline; }

/* projects */
.projects { list-style: none; padding-left: 0; margin: 0; display:flex; flex-direction:column; gap:8px; }
.proj-link { margin-left: 8px; font-size:0.9rem; color:var(--accent); }

/* grid-3 */
.grid-3 { display:grid; grid-template-columns: repeat(3, 1fr); gap: 16px; }
.grid-3 .card { margin-bottom: 0; }

/* contact */
.contact-card p { margin: 6px 0; color:var(--muted); }
.contact-actions a { margin-right: 12px; color:var(--accent); }

/* footer */
.footer { text-align:center; margin-top: 18px; color: var(--muted); }

/* modal placeholder removed (no BibTeX) */

/* reveal animation handled via class in-view */

/* ========== Responsive ========== */
@media (max-width: 980px) {
  .layout { grid-template-columns: 1fr; padding: 0 14px; gap: 12px; }
  .sidebar { order: 2; display:block; }
  .mobile-toggle { display:block; }
  .topbar { padding: 12px; }
  .logo { width:48px; height:48px; }
  .name { font-size:1rem; }
  .grid-3 { grid-template-columns: 1fr; }
}

a {
  color: var(--accent);
}

a:hover {
  color: #D4AF37;
}

</style>
